import React from 'react';
import teacher from '../assets/teacher.png';

const containerStyle = {
  backgroundImage: `url(${teacher})`, 
  backgroundRepeat: 'no-repeat', 
  backgroundPosition: 'center', 
  backgroundSize: '100%',
  height: 0,
  paddingTop: 'calc(100% / 1.6)', // Aspect ratio is 16:10
  position: 'relative'
};
const whiteboardStyle = {
  position: 'absolute', 
  top: '18%', 
  left: '5%', 
  width: '28%', 
  height: '37%', 
  overflow: 'auto', 
  fontSize: '1.25vw'
};

function getContentForMode(mode) {
  if (mode === 'test') {
    return (
      <>
      <p>
        TEST MODE
      </p>
      <p>
        TEST MODE
      </p>
      </>
    )
  }
  return (
    <>
      <p>
      You are Achilles. In an attempt to stop the Greek army from winning the Trojan War, 
      Aphrodite has wiped your memory! You need to piece together what has happened to you so far and see where Fate takes you next. 
      You need to act, speak and think like Achilles.
      </p>
      <p>
      You do not need to carry out every task. 
      But you must complete at least one activity from each section in order to summon Hermes who will have your next mission.
      </p>
      <p>
      On your way, you should aim to collect as many Nectar Points as possible. You don’t know when you’ll need to use them to please the gods!
      </p>
    </>
  );
}

class HomePage extends React.Component {
  render() {
    const { params } = this.props;
    const { mode } = params;
    return (
      <div style={containerStyle}>
        <div style={whiteboardStyle}>
          {getContentForMode(mode)}
        </div>
      </div>
   );
  }
}

export default HomePage;